(function () {
    'use strict';
    var angular = require('angular');
    require('./step7.component.service');
    require('./step7.component.scss');
    // require('../save-exit.modal/overlayConfirmation.modal.component.js');

    function step7Controller($state, step7Service, $uibModal, $anchorScroll, $rootScope, $q, journeyService, analyticService, $filter) {

        var vm = this;
        var options = {};
        var application = journeyService.getDataFromStorage();

        vm.$onInit = function () {
            $rootScope.$broadcast('hide-step-header', {
                hideHeader: false
            });
            $anchorScroll();
            $rootScope.showSelectedCard = true;
            $rootScope.showARN = false;
            $rootScope.stepsHeader.currentStep = 3;
            vm.selectedCard = journeyService.getSelectedCard();
            vm.isStaffJourney = journeyService.isStaffJourney();
            vm.model = {};
            var promisses = [];
            promisses.push(journeyService.getCountries());
            promisses.push(journeyService.getErrorMessages());
            $q.all(promisses).then(function (responses) {
                vm.countries = responses[0];
                vm.errorMessages = responses[1].step6;
                var temp = {
                    countryName: "Please select",
                    countryCode: null
                }
                vm.countries.splice(0, 0, temp);

                vm.model = step7Service.getUserDetails();

                vm.model.nationality = vm.model.nationality;

                vm.model.selectedSecondCountry = vm.model.selectedSecondCountry;

                vm.model.selectedThirdCountry = vm.model.selectedThirdCountry;

                // if (!vm.model.selectedCountry)
                //     vm.model.selectedCountry = vm.model.selectedCountry ? $filter('filter')(vm.countries, {
                //         "countryName": vm.model.selectedCountry
                //     })[0] : vm.countries[0];
            });
            analyticService.pageList.review.internal_reference_id = journeyService.getDataFromStorageContact().adobeId;
            analyticService.trackPageLoad(analyticService.pageList.review);
        };

        vm.next = function () {
            step7Service.sendReviewData(false).then(function (result) {
                if (result.success) {
                    $state.go('app.journey.s8-consent',{'cardKey':vm.selectedCard.key});
                }else if (result.errorCode === 10006) {
                    //arun else if condition
                    //vm.errMsg = "Sorry! there seems to be some technical error. Please try again";
                    $state.go('app.journey.s1-cards');
                }
            });
            analyticService.buttonList.buttonClick.event_content = "continue";
            analyticService.trackUserAction(analyticService.buttonList.buttonClick);
        };

        vm.save = function () {
            var modalInstance = $uibModal.open({
                backdrop: 'static',
                keyboard: false,
                modalFade: true,
                component: "overlaySaveConfirmation",
                windowClass: 'card-detail-modal'
            });
            modalInstance.result.then(function (isSelected) {
                if (isSelected) {
                    step7Service.sendReviewData(true).then(function (result) {
                        if (result.success) {
                            $state.go('app.save-exit', {
                                saveData: {
                                    name: step7Service.fullName
                                },cardKey:vm.selectedCard.key
                            });
                        }
                    });
                }
            }, function () { });
            analyticService.buttonList.buttonClick.event_content = "save for later";
            analyticService.trackUserAction(analyticService.buttonList.buttonClick);
        };

        vm.goback = function () {
            $state.go('app.journey.s6-incomeDetails',{'cardKey':vm.selectedCard.key});
            analyticService.buttonList.buttonClick.event_content = "back";
            analyticService.trackUserAction(analyticService.buttonList.buttonClick);
        };
    }

    var step7Comp = angular.module('journey.module').component('step7', {
        template: require('./step7.component.html'),
        controller: ['$state', 'step7Service', '$uibModal', '$anchorScroll', '$rootScope', '$q', 'journeyService', 'analyticService', '$filter', step7Controller]
    });
    module.exports = step7Comp.name;
})();



